.login {
  display: flex;
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background: #222f3e;
  .loginBox{
    margin: auto;
    max-width: 100%;
    width: 400px;
    overflow: hidden;
    .logo{
      width: 400px;
      max-width: 100%;
      margin-bottom: 20px;
      border-bottom: 2px solid #888;
      img{
        width: 100%;
      }
    }
    form{
      color: #ffffff;
      label{
        display: block;
        margin-bottom: 20px;
        width: 100%;
        .field{
          display: block;
          margin-bottom: 5px;
        }
        input{
          width: 100%;
          font-size: 1em;
          color: #222f3e;
          padding: 15px;
          border: none;
          border-bottom: 1px solid rgb(46, 133, 221);
          background: rgb(246,248,250);
          border-radius: 5px;
          outline: none;
        }
      }
      .formAction {
        display: flex;
        justify-content: space-between;
        padding-bottom: 20px;
        border-bottom: 2px solid #888;
        align-items: flex-end;
        .link{
          color: #888;
          font-weight: bold;
        }
      }
    }
  }
}